body {
  background-color: #e5e5e5;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App a {
  text-decoration: none; }

.App .App-body {
  margin: 0 20px; }

.App .buttons {
  display: flex;
  margin: 0 auto;
  width: 300px; }

.App .btn {
  background-color: #268fb2;
  border: none;
  border-radius: 5px;
  padding: 10px;
  color: white;
  font-weight: bold;
  margin: 10px 5px;
  font-size: 16px;
  text-transform: uppercase; }

.App .btn-secondary {
  background-color: gray; }

.App .box-title {
  background-color: lightblue;
  padding: 15px 20px;
  border-radius: 5px;
  border: 5px solid white;
  margin: 10px 0; }
  .App .box-title h2 {
    color: white;
    margin: 0px; }
  .App .box-title__name {
    color: #268fb2; }

.App-header {
  display: flex;
  justify-content: center;
  align-items: center; }
  .App-header .App-logo {
    height: 40px;
    margin-right: 20px; }
  .App-header .App-branch {
    color: #000;
    font-size: 24px; }
    .App-header .App-branch .App-link {
      color: #000; }

.categories {
  margin: 10px auto;
  max-width: 320px; }
  .categories__list {
    counter-reset: li;
    list-style: none;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0; }
    .categories__list__item {
      border-radius: 5px;
      background-color: white;
      display: flex;
      position: relative;
      padding: 5px 25px;
      margin: 8px 0;
      margin-left: 20px;
      text-transform: capitalize; }
      .categories__list__item:hover {
        background-color: lightblue;
        font-weight: bold;
        color: white; }
      .categories__list__item::before {
        content: counter(li);
        counter-increment: li;
        align-items: center;
        background-color: lightblue;
        border: 5px solid white;
        border-radius: 50%;
        position: absolute;
        color: white;
        display: flex;
        font-weight: bold;
        justify-content: center;
        margin-right: 10px;
        height: 30px;
        width: 30px;
        left: -20px;
        top: -4px; }

.progress-bar {
  text-align: center;
  background-color: lightblue;
  padding: 15px 20px;
  border-radius: 5px;
  border: 1px solid #82cfe8;
  margin: 10px 0; }
  .progress-bar h2 {
    margin: 0px;
    color: #268fb2; }

.error-message {
  background-color: lightcoral;
  padding: 15px 20px;
  border-radius: 5px;
  border: 1px solid lightsalmon; }
  .error-message h2 {
    color: darkred;
    margin: 0; }

.jokes {
  max-width: 768px;
  margin: 20px auto; }
  .jokes .joke {
    padding: 0; }
    .jokes .joke__item {
      background-color: white;
      border-radius: 5px;
      margin: 10px 0;
      padding: 20px;
      list-style: none;
      position: relative; }
    .jokes .joke .category-content {
      display: flex;
      align-items: center; }
      .jokes .joke .category-content__icon {
        height: 100%;
        margin-right: 10px; }
      .jokes .joke .category-content__date {
        color: gray;
        font-size: 12px;
        position: absolute;
        top: 5px;
        right: 5px; }

.warning-message {
  background-color: lightyellow;
  padding: 15px 20px;
  border-radius: 5px;
  border: 1px solid lightgoldenrodyellow;
  text-align: center; }
  .warning-message h2 {
    color: brown;
    margin: 0; }
  .warning-message p {
    color: darkgoldenrod;
    font-size: 24px;
    margin: 0; }

